<template>
  <transition name="slide">
    <div
      v-if="visible"
      class="fixed inset-y-0 right-0 w-full sm:w-96 bg-white shadow-xl z-50 flex flex-col"
    >
      <!-- Ìó§Îçî -->
      <div class="flex justify-between items-center p-4 border-b">
        <h3 class="text-lg font-semibold text-gray-800">{{ title }}</h3>
        <button class="text-gray-600 hover:text-black" @click="$emit('close')">‚úï</button>
      </div>

      <!-- Í≤ÄÏÉâ ÏòÅÏó≠ -->
      <div v-if="showSearch" class="p-4 border-b">
        <div class="flex items-center border rounded-md px-3 py-2">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="h-5 w-5 text-gray-400 mr-2"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
            />
          </svg>
          <input
            v-model="srchParam"
            type="text"
            placeholder="Í≤ÄÏÉâÏñ¥ ÏûÖÎ†•"
            class="flex-1 outline-none"
            @keyup.enter="onSearch"
          />
        </div>
      </div>

      <!-- ‚úÖ ÏΩòÌÖêÏ∏† ÏòÅÏó≠ -->
      <div class="flex-1 overflow-y-auto p-4 mb-20">
        <!-- Î°úÎî© Ï§ë -->
        <div v-if="isLoading" class="flex flex-col items-center justify-center text-gray-500 mt-20">
          <svg
            class="animate-spin h-10 w-10 text-green-600 mb-3"
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
          >
            <circle
              class="opacity-25"
              cx="12"
              cy="12"
              r="10"
              stroke="currentColor"
              stroke-width="4"
            ></circle>
            <path
              class="opacity-75"
              fill="currentColor"
              d="M4 12a8 8 0 018-8v4a4 4 0 00-4 4H4z"
            ></path>
          </svg>
          <p class="text-sm">Í≤ÄÏÉâ Ï§ëÏûÖÎãàÎã§...</p>
        </div>

        <!-- ÏóêÎü¨ Î∞úÏÉù -->
        <div
          v-else-if="isError"
          class="flex flex-col items-center justify-center text-red-500 mt-20"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="h-12 w-12 mb-3"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M12 9v2m0 4h.01M12 5a7 7 0 100 14a7 7 0 000-14z"
            />
          </svg>
          <p class="font-semibold">Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.</p>
          <p class="text-sm text-gray-500">Ïû†Ïãú ÌõÑ Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.</p>
        </div>

        <!-- Í≤∞Í≥º ÏûàÏùÑ Îïå -->
        <template v-else-if="hasResults">
          <label
            v-for="(item, idx) in items"
            :key="item[keyField] || idx"
            class="flex items-center justify-between border rounded-md px-4 py-3 mb-2 hover:border-green-600 cursor-pointer"
          >
            <span>{{ item[labelField] }}</span>
            <input
              :type="multiple ? 'checkbox' : 'radio'"
              :name="multiple ? 'multi-select[]' : 'single-select'"
              :checked="isSelected(item)"
              class="ml-auto accent-green-600 w-5 h-5"
              @change="toggleSelection(item)"
            />
          </label>
        </template>

        <!-- Í≤∞Í≥º ÏóÜÏùÑ Îïå -->
        <template v-else>
          <div class="flex flex-col items-center justify-center text-gray-500 mt-20">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              class="h-16 w-16 text-gray-400 mb-4"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                d="M21 21l-5.2-5.2m0 0A7 7 0 1010 17a7 7 0 005.8-2.8z"
              />
            </svg>
            <p class="text-lg font-semibold">Í≤ÄÏÉâ Í≤∞Í≥ºÍ∞Ä ÏóÜÏäµÎãàÎã§.</p>
            <p class="text-sm">Í≤ÄÏÉâÏñ¥Î•º ÌôïÏù∏Ìï¥ Ï£ºÏÑ∏Ïöî.</p>
          </div>
        </template>
      </div>

      <!-- ‚úÖ ÌïòÎã® Í≥†Ï†ï Î≤ÑÌäº -->
      <div class="absolute bottom-0 left-0 w-full p-4 bg-white border-t">
        <button
          class="w-full bg-green-700 text-white py-3 rounded-md hover:bg-green-800 transition"
          @click="$emit('confirm', selectedItems)"
        >
          ÌôïÏù∏
        </button>
      </div>
    </div>
  </transition>
</template>

<script setup>
/* eslint-disable */
import { ref, watch, defineProps, defineEmits } from 'vue'

const props = defineProps({
  visible: Boolean,
  title: { type: String, default: 'ÏÇ¨Ïù¥Îìú Î©îÎâ¥' },
  showSearch: { type: Boolean, default: false },
  keyword: { type: String, default: '' },
  hasResults: { type: Boolean, default: true },
  isLoading: { type: Boolean, default: false },
  isError: { type: Boolean, default: false },
  multiple: { type: Boolean, default: false }, // ‚úÖ Îã§Ï§ë ÏÑ†ÌÉù Ïó¨Î∂Ä
  items: { type: Array, default: () => [] }, // ‚úÖ ÌëúÏãúÌï† Î¶¨Ïä§Ìä∏
  keyField: { type: String, default: 'id' }, // ‚úÖ item Í≥†Ïú†ÌÇ§ ÌïÑÎìúÎ™Ö
  labelField: { type: String, default: 'label' }, // ‚úÖ item ÌëúÏãú ÌïÑÎìúÎ™Ö
  modelValue: { type: [Array, Object, String, Number], default: () => [] },
})

const emit = defineEmits(['close', 'search', 'confirm', 'update:modelValue'])
const srchParam = ref(props.keyword)
const selectedItems = ref(props.multiple ? [...props.modelValue] : props.modelValue)

watch(srchParam, (v) => emit('search', v))

// ‚úÖ Í≤ÄÏÉâ Ïã§Ìñâ
function onSearch() {
  emit('search', srchParam.value)
}

// ‚úÖ Ìï≠Î™© ÏÑ†ÌÉù Ïó¨Î∂Ä ÌåêÎã®
function isSelected(item) {
  if (props.multiple) {
    return selectedItems.value.some((i) => i[props.keyField] === item[props.keyField])
  } else {
    return selectedItems.value && selectedItems.value[props.keyField] === item[props.keyField]
  }
}

// ‚úÖ ÏÑ†ÌÉù ÌÜ†Í∏Ä
function toggleSelection(item) {
  if (props.multiple) {
    const exists = selectedItems.value.find((i) => i[props.keyField] === item[props.keyField])
    if (exists) {
      selectedItems.value = selectedItems.value.filter(
        (i) => i[props.keyField] !== item[props.keyField]
      )
    } else {
      selectedItems.value.push(item)
    }
  } else {
    selectedItems.value = item
  }
  emit('update:modelValue', selectedItems.value)
}
</script>

<style scoped>
.slide-enter-active,
.slide-leave-active {
  transition: transform 0.3s ease;
}
.slide-enter-from,
.slide-leave-to {
  transform: translateX(100%);
}
</style>
