const userIdFcs = ref(null);
const userPwConfirmFcs = ref(null);
const userPwFcs = ref(null);
const userNmFcs = ref(null);
const mblNoFcs = ref(null);
const smsAuthReqBtnFcs = ref(null);
const certNoFcs = ref(null);
