watch(userPw, (newVal) => {
  if (proxy.$util.validatePasswordRule(newVal)) {
    pwMsg.value = "‚úÖ";
  } else {
    pwMsg.value = "‚ùå";
  }
});

watch(userPwConfirm, (newVal) => {
  if (newVal == userPw.value) {
    pwConfirmMsg.value = "‚úÖ";
  } else {
    pwConfirmMsg.value = "‚ùå";
  }
});

